<template>
  <div>
    <fTitle>
      <template #title>InfiniteScroll 无限滚动</template>
      <template #introduce>滚动至底部时，加载更多数据</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法</template>
      <template #introduce>在要实现滚动加载的列表上上添加v-infinite-scroll，并赋值相应的加载方法，可实现滚动到底部时自动执行加载方法。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <ul class="infinite-list" v-infinite-scroll="load" style="overflow:auto">
    <li v-for="i in count" :key="i" class="infinite-list-item">{{ i }}</li>
  </ul>
</div>
    </el-col></el-row>
    </div></div>
    <sTitle>
      <template #title>禁用加载</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content"><div>
    <ul style="overflow:auto"
      class="list"
      v-infinite-scroll="load2"
      infinite-scroll-disabled="disabled">
      <li v-for="i in count1" :key="i" class="list-item">{{ i }}</li>
    </ul>
    <p v-if="loading">加载中...</p>
    <p v-if="noMore">没有更多了</p>
  </div> </div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      count: 0,
      count1: 10,
      loading: false
    }
  },
  methods: {
    load () {
      if (this.count >= 16) {
        return false
      }
      this.count += 2
      console.log('触发了加载函数')
    },
    load2 () {
      this.loading = true
      setTimeout(() => {
        this.count1 += 2
        this.loading = false
      }, 2000)
    }
  },
  computed: {
    noMore () {
      return this.count1 >= 20
    },
    disabled () {
      return this.loading || this.noMore
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;
        .infinite-list{
          width: 100%;
          height: 200px;
          li{
          width: 90%;
          height: 60px;
          background-color:#e8f3fe;
          color: #7dbcfc;
          margin-top: 10px;
          }
        }
        p{
            margin-right: 80px;
          }
        .list{
          width: 100%;
          height: 200px;

          li{
            width: 90%;
          height: 60px;
          background-color:#fff6f6;
          color: #ff8484;
          margin-top: 10px;
          }

        }
        }
    }}
</style>
